<template>
  <el-form
    class="form"
    :inline="true"
    v-loading="loading"
    @submit.native.prevent="onSubmit"
    :model="form"
    label-width="150px"
  >
    <el-row :gutter="24">
      <el-col>
        <el-form-item label="登录背景">
          <el-upload
            :headers="myHeaders"
            :action="'/v1/api/api/api/upload'"
            :on-remove="remove_login"
            :on-success="success_login"
            :before-upload="fileSuccessBefore"
            :data="{ a: 1 }"
            :file-list="file_login"
            list-type="picture"
          >
            <el-button size="small" type="infor">点击上传</el-button>
            <div slot="tip" style="color: red" class="el-upload__tip">
              尺寸:宽720px 高:1280px 格式:png、jpg、jpeg、gif
            </div>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="注册">
          <el-upload
            :headers="myHeaders"
            :action="'/v1/api/api/api/upload'"
            :on-remove="remove_reg"
            :on-success="success_reg"
            :before-upload="fileSuccessBefore"
            :file-list="file_reg"
            list-type="picture"
          >
            <el-button size="small" type="infor">点击上传</el-button>
            <div slot="tip" style="color: red" class="el-upload__tip">
              尺寸:宽720px 高:1280px 格式:png、jpg、jpeg、gif
            </div>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="找回密码">
          <el-upload
            :headers="myHeaders"
            :action="'/v1/api/api/api/upload'"
            :on-remove="remove_forget"
            :on-success="success_forget"
            :before-upload="fileSuccessBefore"
            :file-list="file_forget"
            list-type="picture"
          >
            <el-button size="small" type="infor">点击上传</el-button>
            <div slot="tip" style="color: red" class="el-upload__tip">
              尺寸:宽720px 高:1280px 格式:png、jpg、jpeg、gif
            </div>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      form: {},
      file_login: [],
      file_reg: [],
      file_forget: [],
      loading: false,
      myHeaders: { Authorization: 'Bearer '+localStorage.getItem('pstt')},
    }
  },
  computed: {},
  mounted() {
    this.loading = true
    Fetch('/sys/config/infobg', {})
      .then(r => {
        this.form = r.data
        if (r.data.reg) {
          this.file_reg = [
            {
              url: r.data.reg,
              name: r.data.reg.split('/').pop(),
            },
          ]
        }

        if (r.data.forget) {
          this.file_forget = [
            {
              url: r.data.forget,
              name: r.data.forget.split('/').pop(),
            },
          ]
        }

        if (r.data.login) {
          this.file_login = [
            {
              url: r.data.login,
              name: r.data.login.split('/').pop(),
            },
          ]
        }
      })
      .catch(res => {
        Message.error(res.msg)
      })

    this.loading = false
  },
  methods: {
    success_login(res, file) {
      this.form.login = res.data.file
      this.file_login = [file]
    },
    remove_login() {
      this.form.login = ''
      this.file_login = []
    },
    success_reg(res, file) {
      this.form.reg = res.data.file
      this.file_reg = [file]
    },
    remove_reg() {
      this.form.reg = ''
      this.file_reg = []
    },
    success_forget(res, file) {
      this.form.forget = res.data.file
      this.file_forget = [file]
    },
    remove_forget() {
      this.form.forget = ''
      this.file_forget = []
    },
    fileSuccessBefore(file) {
      const uploadLimit = 2
      const filetype = file.name.replace(/.+\./, '')
      const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
      const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
      if (!isRightSize) {
        this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
        return false
      }
      if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
        this.$message.warning({
          message: '请上传后缀名为png、jpg、jpeg、gif的附件',
        })
        return false
      }
      return true
    },
    onSubmit() {
      Fetch3('/sys/config/infobg', {
        ...this.form,
      })
        .then(() => {
          this.loading = false
          Message.success('操作成功')
        })
        .catch(() => {
          this.loading = false
          Message.error('操作失败')
        })
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
